@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400&family=Playfair+Display+SC:wght@400;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{box-sizing:border-box}*,*:before,*:after{font-family:"Open Sans",sans-serif;box-sizing:inherit}h1,h2,h3,h4,h5,h6{font-family:"Playfair Display SC",serif}.header{position:fixed;height:150px;width:100%;height:100px;background:#16A3B2}.header .title{position:absolute;top:50%;transform:translateY(-50%);padding:2rem;color:white}.navigation{margin:0rem 3rem}.navigation ul{list-style-type:none}.navigation li{color:black;padding:0.15rem 0rem}.container{display:grid;grid-template-columns:1fr 400px}.container img{height:100vh;object-fit:cover;width:400px;height:80vh}.container main{overflow:auto;margin-top:100px}.container .mainContent{margin:0rem 3rem 5rem 3rem}.container .theme{height:200px;width:100%;object-fit:cover;margin:1rem}.container .image{display:flex;justify-content:center}.container .sidebanner{position:fixed;right:0;top:100px}.container .overlay{width:100%;position:absolute;height:80vh;background-image:linear-gradient(rgba(255,255,255,0), rgba(255,255,255,0), #fff);z-index:10}table{font-family:arial, sans-serif;border-collapse:collapse;width:100%}td,th{border:1px solid #000;text-align:left;padding:8px}.center{text-align:center}tr:nth-child(even){background-color:#dddddd}.colorScheme{display:flex;justify-content:center}

